/* Main components' CSS go here*/

*, *::before, *::after {
    box-sizing: border-box;
}

.aboutPageContainer {
    height: 100vh;
    background-color: #161616;
    background-size: cover;

    opacity: 0.2;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

/* .homeHeaderContainer {
    height: 100vh;
} */

.AppNavHeader {
    padding: 32px 32px 0;
    height: 60px;
}

.headerNavContainer {
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.AppHeader {
    display: flex;
    justify-content: center;
    margin-top: 150px;
    height: calc(100vh - 150px - 60px);
    align-items: center;
}

.img--socialMedia--container {
    display: flex;
    justify-content: flex-start;
    margin-top: 5rem;
}

.socialMediaContainer {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.socialMediaContainer > a {
    color: black;
}

@media all and (min-width:0px) and (max-width: 480px) {
    .img--socialMedia--container {
        display: flex;
        align-items: center;
        margin-top: 0;
        flex-direction: column;
    }

    .socialMediaContainer {
        height: 100px;
    }

    .AppNavHeader {
        height: 120px;
        padding: 0.75rem 0.75rem 0;
    }

    .AppHeader {
        height: calc(100vh - 150px - 30px);
        margin-top: 0px;
    }

    .headerNavContainer {
        align-items: baseline;
    }
}



/* Header Nav bar CSS goes here*/

/* .headerContainer {
    position: relative;
} */

.span--portfolioTitle {
    font-family: 'Heebo', sans-serif;
    line-height: 1;
    font-weight: 500;
    font-size: 2rem;
    text-decoration: none;
    color: black;
}

.aboutPageBackground .span--portfolioTitle {
    color: white;
}

.span--portfolioTitle:hover {
    color: black;
    text-decoration: none;
}

.span--portfolioTitle span {
    font-family: 'montserrat', sans-serif;
    display: block;
    font-size: 1.5rem;
    transition: color 250ms ease-out;
    color: rgb(11, 87, 114);
}

.navigation {
    float: left;
}

.list--navigation {
    display: flex;
}

.listItem--navBar {
    list-style-type: none;
}

.anchor--navBar {
    font-family: 'montserrat', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    text-decoration: none;
    letter-spacing: 1px;
    padding-left: 15px;
    padding-right: 15px;
}

.homePageContainer .anchor--navBar:hover, .homePageContainer .span--portfolio:hover {
    color: #ad0000;
    text-decoration: none;
}

.homePageContainer .span--portfolioTitle span, .anchor--navBar {
    color: rgb(11, 87, 114);
}

/* Header Container CSS goes here*/

.whatIDo {
    font-family: "Heebo", "Helvetica Neue", Helvetica, sans-serif;
    animation: fadeIn 2s forwards;
    font-size: 2.5rem;
}

@media all and (min-width:0px) and (max-width: 480px) {
    .whatIDo {
        font-size: 1.5rem;
        text-align: center;
    }

    .span--portfolioTitle {
        font-size: 1.5rem;
    }
}


.h1--colorText {
    color: rgb(11, 87, 114);
}

.containerTitle {
    position: relative;
    display: inline-block;
    color: rgb(11, 87, 114);
}

.containerTitle::before {
    content: '';
    height: 100%;
    display: block;
    position: absolute;
    background: white;
    z-index: -1;
    left: -20px;
    right: -20px;
}

.containerTitle::after {
    content: '';
    height: 100%;
    width: 100vw;
    display: block;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    background: rgb(11, 87, 114);
    z-index: -2;
}

.containerContentPersonal {
    text-align: center;
}

@media all and (min-width:0px) and (max-width: 480px) {
    .containerTitle::before {
        left: 20px;
        right: 20px;
    }

    .card__img {
        height: 200px;
        width: 250px;
    }
}

.containerContent {
    text-align: center;
    max-width: 800px;
    margin: auto;
    padding: 16px 0 16px 0;
}

.containerContent p {
    font-size: 1.5rem;
    font-weight: lighter;
}

.containerContentPersonal p {
    font-size: 1.5rem;
    font-weight: lighter;
}

.cardContainer {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.card {
    position: relative;
    background: gray;
    margin: 1em;
}

.card::before {
    content: '';
    position: absolute;
    top: 1.25em;
    bottom: 1.25em;
    left: 1.25em;
    right: 1.25em;
    transform: scale(0, 1);
    transition: transform ease-out 250ms;
    border-top: 1px solid rgb(11, 87, 114);
    border-bottom: 1px solid rgb(11, 87, 114);
}

.card:hover::before {
    transform: scale(1.05, 1);
}

.card:after {
    content: '';
    position: absolute;
    top: 1.25em;
    bottom: 1.25em;
    left: 1.25em;
    right: 1.25em;
    transform: scale(1, 0);
    transition: transform ease-out 250ms;
    border-left: 1px solid rgb(11, 87, 114);
    border-right: 1px solid rgb(11, 87, 114);
}

.card:hover::after {
    transform: scale(1, 1.05);
}

.card:hover .card__text{
    opacity: 1;
}

.card:hover {
    cursor: pointer;
}

.card__img {
    display: block;
    transition: opacity ease-out 250ms;
    opacity: 1;
}

.card:hover .card__img {
    opacity: 0.3;
}

.card__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    opacity: 0;
    color: rgb(5, 60, 80);
    transition: opacity ease-out 250ms;
}

.containerContentPersonal {
    margin: auto;
}

/*

    ABOUT PAGE CSS GOES HERE

*/

.aboutPageBackground {
    background-color: rgba(0,0,0,0.9);
    min-height: 100vh;
}

.aboutPageContainer .span--portfolioTitle {
    color: white;
}

.aboutPageContainer .span--portfolioTitle span, .aboutPageContainer .anchor--navBar {
    color: rgb(14, 168, 222);
}

.aboutPageContainer .span--portfolioTitle span:hover, .aboutPageContainer .anchor--navBar:hover {
    color: rgb(11, 87, 114);
    text-decoration: none;
}

.AboutContainer {
    padding: 100px 50px 100px 50px;
}

.container__about-me {
    color: white;
    text-align: center;
}

.container__about-me span {
    font-size: 1.5rem;
    font-weight: 200;
}

.container__about-me strong {
    color: rgb(14, 168, 222);
}

/*
    SKILLS CSS GO HERE
*/

.skillsContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 5em;
}

.skill {
    margin: 1em;
}

.skillsLine {
    animation: lineFade 2s 1;
}

@keyframes lineFade{
    from {
        stroke-dasharray: 350;
        stroke-dashoffset: 350;
    }
    to {
        stroke-dashoffset: 0;
    }
}

@media all and (min-width:0px) and (max-width: 480px) {
    .skillsContainer {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}